export default {
  components: {},
  data() {
    return {
      columns: [
        { label: '用户名', width: 180, field: 'username' },
        { label: '组别', width: 180, field: 'role', slot: 'role' },
        {
          label: '创建时间',
          width: 180,
          field: 'createTime',
          slot: 'createTime'
        },
        {
          label: '封号',
          width: 150,
          field: 'disabled',
          slot: 'disabled'
        },
        { label: '操作', slot: 'operate' }
      ],
      total: 0,
      pageSize: 2,
      users: [],
      current: 1
    };
  },
  mounted() {
    this.getAllUsers(this.current);
  },
  methods: {
    changeCurrentPage(current) {
      this.current = current;
      this.getAllUsers(current);
    },
    getAllUsers(current) {
      const { response } = this.$http({
        url: `/allUsers/`,
        data: {
          pageSize: this.pageSize,
          currentPage: current
        },
        showSuccessMsg: false,
        showErrorMsg: true,
        jumpLogin: true
      });

      return response.then(
        (data) => {
          this.users = data.list;
          this.total = data.total;
        },
        (e) => {}
      );
    },
    addArticle() {
      this.$router.push('/admin/post');
    },
    viewArticle(article) {
      this.$router.push(`/admin/article/${article._id}`);
    },
    toggle(status, id) {
      const { response } = this.$http({
        url: `/updateUserByAdmin/${id}`,
        method: 'put',
        data: {
          disabled: status
        },
        showSuccessMsg: '操作成功',
        showErrorMsg: true
      });

      response.then(
        () => {
          this.getAllUsers(this.current);
        },
        () => {}
      );
    },
    edit(user) {
      this.$router.push(`/admin/user/${user._id}`);
    },
    config(article) {
      this.$router.push(`/admin/article/config/${article._id}`);
    },
    openDialog() {
      this.$alert('输入用户名', {
        buttons: {
          cancel: {
            text: '取消',
            value: null,
            visible: true,
            closeModal: true
          },
          confirm: {
            text: '保存',
            value: null,
            visible: true,
            closeModal: true
          }
        },
        content: {
          element: 'input',
          attributes: {
            placeholder: '输入用户名'
          }
        }
      }).then((value) => {
        // 点击取消
        if (value !== null) {
          if (value !== '') {
            this.addUser(value);
          } else {
            // 标签名输入为空
            this.$notify.warning('用户名不能为空');
          }
        }
      });
    },
    addUser(username) {
      const { response } = this.$http({
        url: `/addUserByAdmin`,
        method: 'post',
        data: {
          username
        },
        showSuccessMsg: '操作成功',
        showErrorMsg: true
      });

      response.then(
        () => {
          this.getAllUsers(this.current);
        },
        () => {}
      );
    }
  }
};
